import { AdminSidebar } from "@/components/shared/Sidebar";

export default function AdminLayout({
    children,
}: {
    children: React.ReactNode;
}) {
    return (
        <div className="flex flex-row h-screen w-full bg-background overflow-hidden relative">
            <div className="w-64 shrink-0 h-full z-40">
                <AdminSidebar />
            </div>
            <main className="flex-1 h-full overflow-y-auto bg-zinc-50/30 dark:bg-black relative z-10 border-l border-zinc-200/10">
                <div className="p-8 max-w-7xl mx-auto w-full">
                    {children}
                </div>
            </main>
        </div>
    );
}
